body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.steps-content {
  margin-top: 16px;
  margin-left: 16px;
  margin-right: 16px;
  border: 1px dashed #e9e9e9;
  border-radius: 6px;
  background-color: #fafafa;
  min-height: 200px;
  text-align: center;
  padding-top: 80px;
}

.steps-action {
  margin-top: 24px;
  margin-left: 24px;
}

.steps-main{
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 30px;
}

.media{
  border: 2px solid #111;
  background-color: #111;
}

.pre-style {
  text-align: left;
  margin-left: 20px;
  color: #fff;
}

.social_button a{
  text-decoration: none;
  color: #fff;
}
.social_button :hover a :hover{
  text-decoration: none;
  color: #fff;
}

